.footer
  background-color: $asd
  &__content
    padding: 60px 0
    @media (min-width: 577px)
      display: flex
      flex-wrap: wrap
      align-items: center
      width: 100%
      max-width: 690px
      flex: 1 1 100%
      padding: 60px 0
      margin: 0 auto
    @media (min-width: 961px)
      max-width: 100%
    @media (min-width: 1200px)
      position: relative
      max-width: 970px
      padding-top: 90px
    &__links
      text-align: center
      @media (min-width: 577px)
        display: flex
        flex-direction: column
        flex: 1 1 50%
        align-items: flex-start
        justify-content: flex-start
      @media (min-width: 1200px)
        position: absolute
        bottom: 60px
        left: 0
    &__phone
      .phone-link
        display: inline-block
        font-weight: 800
        font-size: 28px
        line-height: 28px
        letter-spacing: -.02em
        color: $sdark-blue
        transition: color .3s
        &:hover
          color: $sblue
    &__email
      margin-top: 18px
      @media (min-width: 577px)
        margin-top: 12px
      @media (min-width: 1200px)
        margin-top: 28px
      .mail-link
        display: inline-block
        font-weight: 500
        font-size: 16px
        line-height: 22px
        text-decoration: underline
        color: $sdark-blue
        transition: color .3s
        &:hover
          color: $sblue
    &__bcall
      margin-top: 18px
      text-align: center
      @media (min-width: 577px)
        display: flex
        flex: 1 1 50%
        align-items: center
        justify-content: flex-end
        margin-top: 0
      @media (min-width: 1200px)
        order: 2
      .fcall-link
        width: 200px
        margin: 0 auto
        padding: 15px 0
        @media (min-width: 577px)
          margin: 0 0 0 auto
    &__menu
      margin-top: 25px
      border-top: 1px solid rgba(#052350, .2)
      @media (min-width: 577px)
        flex: 1 1 100%
        max-width: 100%
        width: 100%
        margin-top: 49px
      @media (min-width: 1200px)
        order: 3
        padding-left: 30%
        margin-top: 40px
      .footer-menu
        &__list
          padding: 12px 0
          @media (min-width: 577px)
            display: flex
            flex-wrap: wrap
            flex-direction: row
            align-items: center
            justify-content: center
            margin: 38px 0 0
          @media (min-width: 1200px)
            justify-content: flex-end
        &__item
          margin-bottom: 18px
          display: block
          width: 100%
          text-align: center
          @media (min-width: 577px)
            display: inline-block
            flex: 0 0 auto
            margin: 0 12px
            width: auto
            @media (min-width: 1200px)
              margin: 0 16px
          &:last-child
            margin-bottom: 0
            @media (min-width: 1200px)
              margin-right: 0
        &__link
          display: inline-block
          font-size: 16px
          line-height: 20px
          font-weight: 700
          color: $sdark-blue
          transition: color .3s
          &:hover
            color: $sblue
    &__copyright
      text-align: center
      color: rgba($sdark-blue, .5)
      font-size: 14px
      line-height: 16px
      font-weight: 500
      @media (min-width: 577px)
        flex: 1 1 100%
        width: 100%
        max-width: 100%
        margin-top: 38px
      @media (min-width: 1200px)
        order: 4
        text-align: right
        margin-top: 25px
        color: $sdark-blue
        font-weight: 500
        font-size: 16px
        line-height: 20px
    &__descr
      @media (min-width: 1200px)
        display: block !important
        order: 1
        flex: 1 1 50%
        max-width: 50%
        width: 100%
        .block-title
          color: $sdark-blue
        p
          color: $sdark-blue
          font-size: 18px
          line-height: 22px
          font-weight: 500
          letter-spacing: -.02em
          display: inline-block
          margin-top: 14px
