.modal-form-style
  background-color: #fff
  border-radius: 8px
  max-width: 470px
  margin: 0 auto
  .modal-body
    padding: 15px 15px 20px
    @media (min-width: 321px)
      padding: 20px
    @media (min-width: 415px)
      padding: 20px 30px 30px
  .modal-title
    font-family: Oswald, sans-serif
    font-size: 34px
    line-height: 45px
    font-weight: 700
    letter-spacing: -.02em
    color: $dark-blue
    display: block
    max-width: calc(100% - 25px)
  .modal-form
    margin-top: 30px
    .input-group
      margin-bottom: 20px
      &.btn-group
        margin-bottom: 0

.mfp-close-btn-in .modal-form-style .mfp-close
  color: $dark-blue
  opacity: 1 !important
  font-size: 40px
  top: 21px
  right: 2px
  @media (min-width: 321px)
    right: 10px
    top: 27px
  @media (min-width: 415px)
    right: 17px