.input-style,
.txtarea-style {
  display: block;
  width: 100%;
  max-width: 100%;
  background-color: #E2EFFF;
  padding: 13px 12px;
  border: 1px solid transparent;
  border-radius: 4px;
}
.input-style.placeholder,
.txtarea-style.placeholder {
  color: #11489B;
  font-size: 15px;
  line-height: 20px;
  transition: opacity 0.3s;
}
.input-style::-webkit-input-placeholder,
.txtarea-style::-webkit-input-placeholder {
  color: #11489B;
  font-size: 15px;
  line-height: 20px;
  transition: opacity 0.3s;
}
.input-style::-moz-placeholder,
.txtarea-style::-moz-placeholder {
  color: #11489B;
  font-size: 15px;
  line-height: 20px;
  transition: opacity 0.3s;
}
.input-style:-moz-placeholder,
.txtarea-style:-moz-placeholder {
  color: #11489B;
  font-size: 15px;
  line-height: 20px;
  transition: opacity 0.3s;
}
.input-style:-ms-input-placeholder,
.txtarea-style:-ms-input-placeholder {
  color: #11489B;
  font-size: 15px;
  line-height: 20px;
  transition: opacity 0.3s;
}
.input-style:focus,
.txtarea-style:focus {
  background-color: #fff;
  border-color: #1D6AFF;
}
.input-style:focus.placeholder,
.txtarea-style:focus.placeholder {
  opacity: 0.3;
}
.input-style:focus::-webkit-input-placeholder,
.txtarea-style:focus::-webkit-input-placeholder {
  opacity: 0.3;
}
.input-style:focus::-moz-placeholder,
.txtarea-style:focus::-moz-placeholder {
  opacity: 0.3;
}
.input-style:focus:-moz-placeholder,
.txtarea-style:focus:-moz-placeholder {
  opacity: 0.3;
}
.input-style:focus:-ms-input-placeholder,
.txtarea-style:focus:-ms-input-placeholder {
  opacity: 0.3;
}

.txtarea-style {
  min-height: 100px;
}

.check-group {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.check-group input {
  position: absolute;
  width: 0;
  height: 0;
  visibility: hidden;
  opacity: 0;
}
.check-group input:checked + label:after {
  opacity: 1;
}
.check-group input:checked + label:before {
  background-color: #1D6AFF;
}
.check-group label {
  display: inline-block;
  padding-left: 35px;
  position: relative;
  color: #11489B;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: -0.02em;
}
.check-group label:before {
  content: "";
  display: block;
  width: 20px;
  height: 20px;
  border: 2px solid #1D6AFF;
  border-radius: 4px;
  position: absolute;
  left: -1px;
  top: 0px;
  transition: background-color 0.3s;
}
.check-group label:after {
  content: "\e804";
  font-family: Fontello;
  font-style: normal;
  font-weight: normal;
  speak: never;
  margin: 0 auto;
  text-decoration: inherit;
  text-align: center;
  font-variant: normal;
  text-transform: none;
  display: block;
  position: absolute;
  left: 2px;
  top: 5px;
  font-size: 11px;
  line-height: 12px;
  color: #fff;
  transition: opacity 0.3s;
}
.check-group label a {
  color: #11489B;
  text-decoration: underline;
  transition: color 0.3s;
}
.check-group label a:hover {
  color: #2B99FF;
}

.btn-style {
