.input-style,
.txtarea-style
  display: block
  width: 100%
  max-width: 100%
  background-color: $blue-blue
  padding: 13px 12px
  border: 1px solid transparent
  border-radius: 4px
  @include input-placeholder
    color: $dark-blue
    font-size: 15px
    line-height: 20px
    transition: opacity .3s
  &:focus
    background-color: #fff
    border-color: #1D6AFF
    @include input-placeholder
      opacity: .3
.txtarea-style
  min-height: 100px
.check-group
  display: flex
  align-items: center
  justify-content: flex-start
  input
    position: absolute
    width: 0
    height: 0
    visibility: hidden
    opacity: 0
    &:checked + label
      &:after
        opacity: 1
      &:before
        background-color: #1D6AFF
  label
    display: inline-block
    padding-left: 35px
    position: relative
    color: $dark-blue
    font-size: 14px
    line-height: 20px
    letter-spacing: -.02em
    &:before
      content: ''
      display: block
      width: 20px
      height: 20px
      border: 2px solid #1D6AFF
      border-radius: 4px
      position: absolute
      left: -1px
      top: 0px
      transition: background-color .3s
    &:after
      content: '\e804'
      font-family: Fontello
      font-style: normal
      font-weight: normal
      speak: never
      margin: 0 auto
      text-decoration: inherit
      text-align: center
      font-variant: normal
      text-transform: none
      display: block
      position: absolute
      left: 2px
      top: 5px
      font-size: 11px
      line-height: 12px
      color: #fff
      transition: opacity .3s
    a
      color: $dark-blue
      text-decoration: underline
      transition: color .3s
      &:hover
        color: $sblue

.btn-style
  display: block
  width: 100%
  border: none
  text-align: center
  background-color: $dark-blue
  color: #fff
  border-radius: 4px
  font-weight: 500
  font-size: 16px
  line-height: 140%
  letter-spacing: .025em
  padding: 9px 0
  transition: background-color .3s
  &:hover
    background-color: #1D6AFF
  &:active
    background-color: $darky
.btn-style-light
  display: block
  width: 100%
  border: none
  text-align: center
  background-color: $sblue
  color: #fff
  border-radius: 4px
  font-weight: 500
  font-size: 18px
  line-height: 140%
  letter-spacing: .05em
  padding: 11px 0
  transition: background-color .3s
  &:hover
    background-color: #1D6AFF
  &:active
    background-color: $darky

.btn-style-dark
  display: block
  width: 100%
  border: none
  text-align: center
  background-color: $sdark-blue
  color: #fff
  border-radius: 4px
  font-weight: 500
  font-size: 18px
  line-height: 140%
  letter-spacing: .05em
  padding: 11px 0
  transition: background-color .3s
  &:hover
    background-color: #1D6AFF
  &:active
    background-color: $darky

.page-title
  font-family: Oswald, sans-serif
  @include adaptive-value('font-size', 40, 25, 1)
  @include adaptive-value('line-height', 45, 25, 1)
  color: $sdark-blue
  @media (min-width: 640px)
    letter-spacing: .02em
  @media (min-width: 1200px)
    letter-spacing: -.02em
.block-title
  font-family: Oswald, sans-serif
  font-weight: 700
  @include adaptive-value('font-size', 48, 25, 1)
  line-height: 100%
  color: $dark-blue
  @media (min-width: 577px)
    font-size: 48px
    line-height: 48px
    letter-spacing: -.02em
.owl-nav
  position: absolute
  width: calc(100% + 10px)
  height: 0
  top: 50%
  left: -5px
  display: flex
  align-items: center
  justify-content: space-between
  .icon
    display: block
    width: 26px
    height: 26px
    font-size: 25px
    line-height: 25px
    color: $dark-blue
    transition: color .3s
    &:hover
      color: $sblue
  .owl-prev
    .icon
      &:before
        transform: scale(-1,1)
button
  opacity: 1
  transition: opacity .3s
  &:disabled
    opacity: .5

.breadcrumbs
  margin-top: 58px
  @media (min-width: 577px)
    margin-top: 76px
  @media (min-width: 1200px)
    margin-top: 16px
  & + main
    margin-top: 0 !important
  &__list
    display: flex
    flex-wrap: wrap
    width: 100%
    max-width: 100%
    flex: 1 1 100%
    padding: 15px 0 !important
    @include adaptive-value('font-size', 15, 14, 1)
    line-height: 20px
    margin: 0
    a, span
      color: $sdark-blue
    a
      transition: color .3s
      &:hover
        color: $dark-blue
  &__item
    display: inline-flex
    align-items: center
    &:after
      content: '\e802'
      opacity: .3
      font-size: 10px
      line-height: 10px
      font-family: Fontello
      font-style: normal
      font-weight: normal
      speak: never
      display: inline-block
      margin: 0 auto
      text-decoration: inherit
      text-align: center
      font-variant: normal
      text-transform: none
      margin: 0 8px
    &:last-child
      &:after
        display: none

.pagination
  margin: 30px 0
  flex: 1 1 100%
  @media (min-width: 1025px)
    margin-bottom: 80px
  &__list
    display: flex
    max-width: 280px
    margin: 0 auto
    @media (min-width: 1025px)
      margin: 0
    &__item
      flex: 0 0 auto
      width: auto
      margin: 0 auto
    &__link
      display: block
      width: 30px
      background-color: transparent
      border-radius: 4px
      transition: background-color .3s
      text-align: center
      font-weight: 700
      font-size: 15px
      line-height: 20px
      letter-spacing: .02em
      padding: 3px 0
      color: $dark-blue
      &.active
        background-color: $blue-blue
      &:hover
        background-color: $blue-blue
      &.prev-link
        .icon
          &:before
            transform: scale(-1,1)

