@import "settings"                                                                                                      // settings
@import "_mixins/mixins_list"                                                                                           // mixins
@import "_main/_ui.sass"

/* main page */
.section-backcall
  @media (min-width: 961px)
    margin-top: 50px
  @media (min-width: 1200px)
    margin-top: 63px
  .container
    @media (min-width: 577px)
      max-width: 534px
    @media (min-width: 961px)
      max-width: 100%
  &__content
    padding: 15px 18px
    @media (min-width: 961px)
      display: flex
      padding: 15px 0
    &__left-side
      @media (min-width: 961px)
        flex: 1 1 470px
        max-width: 470px
        margin: 0
    &__right-side
      @media (min-width: 961px)
        display: block !important
        flex: 0 1 calc(50% - 55px)
        max-width: 455px
        margin: 0 0 0 auto
        .image-block
          .backcall-image
            margin: 0
            border-radius: 6px
      @media (min-width: 1200px)
        flex: 0 1 calc(50%)
        margin: 0 auto
        position: relative
        left: -11px
        top: 10px
        .image-block
          position: relative
          .backcall-image
            position: relative
            z-index: 1
          &:after
            content: ''
            display: block
            width: 100%
            height: 100%
            position: absolute
            bottom: -10%
            right: -19%
            background-color: $blue2
            border-radius: 12px

  &__title
    color: $dark-blue
    font-family: Oswald, sans-serif
    @include adaptive-value('font-size', 48, 25, 1)
    line-height: 105%
    @media(min-width: 577px)
      font-size: 48px
      line-height: 48px
      letter-spacing: -.02em
  &__subtitle
    @media (min-width: 1200px)
      display: block !important
      color: $dark-blue
      font-size: 24px
      line-height: 32px
      margin-top: 15px
  &__form
    margin-top: 28px
    @media (min-width: 577px)
      margin-top: 43px
    .input-group
      margin-bottom: 18px
      @media (min-width: 577px)
        margin-bottom: 26px
      &.check-group
        margin-top: 20px
        @media (min-width: 577px)
          label
            font-size: 16px
      &.btn-group
        margin-bottom: 0
.section-about
  @media (min-width: 577px)
    margin-top: 10px
  @media (min-width: 1200px)
    margin-top: 11.75rem
  &__content
    @media (min-width: 961px)
      display: flex
      margin: 80px 0 0
    @media (min-width: 1200px)
      margin: 0
    &__left-side
      max-width: calc(100% + 30px)
      width: calc(100% + 30px)
      margin: 0 -15px
      @media (min-width: 577px)
        max-width: 670px
        width: 100%
        margin: 0 auto
      @media (min-width: 961px)
        max-width: 50%
        flex: 1 1 50%
        margin: 0
      .about-image
        width: 110%
        max-width: 110%
        margin: 0 -5%
        @media (min-width: 577px)
          display: block
          width: 100%
          max-width: 100%
          margin: 0 auto
        @media (min-width: 1200px)
          position: relative
          width: 107%
          max-width: 107%
          left: -10%
          top: 4px
    &__right-side
      position: relative
      z-index: 1
      border: 1px solid #11489B
      border-radius: 12px
      padding: 28px 22px
      margin-top: -64px
      @media (min-width: 577px)
        max-width: 570px
        margin: -105px auto 0
        padding: 30px
      @media (min-width: 961px)
        flex: 1 1 50%
        max-width: 50%
        width: 100%
        margin: 0 0 0 auto
        border: none
        border-radius: 0
        &:after
          content: ''
          display: block
          width: 50vw
          height: 100%
          position: absolute
          top: 0
          left: 0
          border: 1px solid #11489B
          border-right: none
          border-radius: 12px 0 0 12px
      @media (min-width: 1200px)
        padding: 90px 35px 98px 80px
  &__descr
    @include adaptive-value('margin-top', 95, 15, 1)
    font-size: 15px
    line-height: 20px
    color: $dark-blue
    letter-spacing: .02em
    @media (min-width: 415px)
      margin-top: 28px
    @media (min-width: 577px)
      font-size: 20px
      line-height: 24px
      font-weight: 500
    @media (min-width: 1200px)
      letter-spacing: -.02em
    p
      margin-bottom: 20px
      @media (min-width: 1200px)
        margin-bottom: 25px
      &:last-child
        margin-bottom: 0

.section-partners
  margin-top: 50px
  @media (min-width: 577px)
    margin-top: 115px
  @media (min-width: 961px)
    margin-top: 80px
  @media (min-width: 1200px)
    margin-top: 120px
  &__title
    text-align: center
  &__subtitle
    text-align: center
    color: $dark-blue
    @include adaptive-value('font-size', 18, 12, 1)
    @include adaptive-value('line-height', 22, 15, 1)
    letter-spacing: .02em
    margin-top: 7px
    @media (min-width: 577px)
      font-size: 18px
      line-height: 22px
      font-weight: 500
      letter-spacing: -.02em
  &__content
    margin-top: 30px
    padding: 0 20px
    .owl-stage
      display: flex
      align-items: stretch
    .slide-item
      height: 100%
      display: flex
      align-items: center
      justify-content: center
      padding: 0 10px
      .partner-image
        display: block
        width: auto
        max-width: 100%
        margin: 0 auto

.section-map
  margin-top: 25px
  padding-top: 38px
  @media (min-width: 577px)
    margin-top: 60px
  @media (min-width: 1200px)
    padding-top: 20px
  &__content
    @media (min-width: 577px)
      position: relative
  &__title
    text-align: center
    position: relative
    z-index: 1
    @media (min-width: 577px)
      position: absolute
      left: 0
      right: 0
      margin: 0 auto
      top: calc(50% - 24px)
  .map-container
    margin-top: -40px
    @media (min-width: 577px)
      margin: 0
      position: relative
      .map-image
        display: block
        margin: 0 auto
        width: 100%
        max-width: 100%
  &__list
    display: flex
    flex-wrap: wrap
    margin: -75px 0 0
    @media (min-width: 415px)
      margin: -115px 0 0
    @media (min-width: 577px)
      display: block
      position: absolute
      top: 0
      left: 0
      width: 100%
      height: 100%
      max-width: 100%
      margin: 0 auto
  &__item
    flex: 1 1 calc(50% - 10px)
    margin: 0 10px 3px 0
    text-align: center
    &:nth-of-type(2n)
      margin-right: 0
    .item-title
      display: inline-block
      font-weight: 600
      color: $dark-blue
      font-size: 14px
      line-height: 20px
      @media (min-width: 577px)
        position: relative
        padding-left: 22px
        min-height: 20px
        span
          display: inline-block
          transition: all .3s
        &:before
          content: ''
          display: block
          width: 18px
          height: 18px
          position: absolute
          left: 0
          top: 1px
          background-color: #1D6AFF
          border: 2px solid #fff
          border-radius: 50%
        &:after
          content: ''
          display: block
          width: 10px
          height: 10px
          background-color: transparent
          border: 2px solid #fff
          border-radius: 50%
          position: absolute
          left: 4px
          top: 5px
    .item-descr
      @media (min-width: 577px)
        display: block
        opacity: 0
        color: $sdark-blue
        font-size: 14px
        line-height: 20px
        letter-spacing: .02em
        position: absolute
        left: 20px
        top: 8px
        width: 210px
        background-color: #fff
        box-shadow: 0px 10px 20px rgb(0 117 225 / 16%)
        border: 1px solid #E2EFFF
        border-radius: 0px 12px 12px 12px
        padding: 25px 10px 10px 10px
        z-index: 10
        transition: opacity .3s
    &:last-child
      margin-bottom: 0
    @media (min-width: 577px)
      position: absolute
      margin: 0
      &.active
        z-index: 15
        .item-title span
          position: relative
          z-index: 11
          transform: translate(11px,12px)
        .item-descr
          opacity: 1
.section-news
  margin: 45px auto
  max-width: 395px
  @media (min-width: 577px)
    max-width: 100vw
    margin: 78px auto 70px auto
    .container
      padding: 0
  @media (min-width: 961px)
    max-width: 100%
    .container
      padding: 0 15px
  @media (min-width: 1200px)
    margin: 50px auto 132px
  .linkrow
    display: flex
    flex-wrap: wrap
    align-items: flex-end
    width: 100%
    justify-content: space-between
    @media (min-width: 577px)
      max-width: 690px
      margin: 0 auto
    @media (min-width: 961px)
      max-width: 100%
    .seeall-link
      display: inline-flex
      align-items: center
      justify-content: flex-start
      font-weight: 600
      font-size: 15px
      line-height: 22px
      letter-spacing: .02em
      color: $dark-blue
      transition: color .3s
      margin-bottom: 0px
      .icon
        line-height: 16px
        position: relative
        left: 3px
        margin-left: 3px
        transition: transform .3s
      &:hover
        color: $sdark-blue
        .icon
          transform: translateX(5px)
  &__content
    margin-top: 26px
    @media (min-width: 577px)
      display: flex
      width: 100%
      flex: 1 1 auto
      margin-top: 42px
      overflow-x: scroll
      @include scrollbars(0, $scrl-bar_track, $scrl-bar_thumb, $scrl-bar_thumbh, 0)
    @media (min-width: 1200px)
      overflow: visible
    &__item
      margin-bottom: 50px
      position: relative
      @media (min-width: 1200px)
        &:after
          content: ''
          display: block
          width: calc(100% + 16px)
          height: calc(100% + 16px)
          position: absolute
          top: -8px
          left: -8px
          border-radius: 12px
          border: 1px solid transparent
          transition: border-color .3s
      @media (min-width: 577px)
        flex: 0 0 290px
        max-width: 290px
        width: 100%
        margin: 0 20px 0 0
        &:nth-of-type(1)
          margin-left: calc((100vw - 690px) / 2)
      @media (min-width: 961px)
        flex: 1 1 calc(33.3% - 7px)
        max-width: calc(33.3%  - 7px)
        margin-right: 11px
        &:nth-of-type(1)
          margin-left: 0
        &:nth-of-type(3n)
          margin-right: 0
      @media (min-width: 1200px)
        flex: 1 1 calc(33.3% - 20px)
        max-width: calc(33.3% - 20px)
        margin-right: 30px
      .item-link
        display: block
        position: relative
        z-index: 1
        border-radius: 6px
        overflow: hidden
        display: block
        min-height: 160px
        @media (min-width: 577px)
          min-height: 150px
        &:after
          content: ''
          display: block
          width: 100%
          height: 100%
          position: absolute
          top: 0
          left: 0
          background: linear-gradient(180deg, rgba(5, 35, 80, 0) 0%, rgba(5, 35, 80, 0.4) 100%)
          transition: opacity .3s
          opacity: 1
        .news-image
          display: block
          width: auto
          max-width: 100%
          margin: 0
          position: absolute
          left: 0
          top: 0
          height: 100%
        .date
          position: absolute
          bottom: 0
          left: 0
          display: inline-block
          background: rgba(5, 35, 80, 0.7)
          border-radius: 0px 4px
          color: #fff
          font-weight: 700
          font-size: 12px
          line-height: 15px
          letter-spacing: .02em
          padding: 4px 6px
          z-index: 1
        &:hover
          &:after
            opacity: 0
      .item-descr
        position: relative
        z-index: 1
        margin-top: 10px
        max-width: 350px
        @media (min-width: 1200px)
          margin-top: 8px
          max-width: 100%
        .item-title
          font-weight: 700
          @include adaptive-value('font-size', 18, 15, 1)
          @include adaptive-value('line-height', 22, 21, 1)
          letter-spacing: .02em
          color: $sdark-blue
          transition: color .3s
          @media (min-width: 1200px)
            letter-spacing: 0
            font-size: 18px
            line-height: 22px
        .descr-block
          color: $sdark-blue
          @include adaptive-value('font-size', 15, 14, 1)
          @include adaptive-value('line-height', 22, 18, 1)
          letter-spacing: .02em
          margin-top: 8px
          @media (min-width: 1200px)
            margin-top: 6px
      &:hover
        &:after
          border-color: $dark-blue
        .item-link
          &:after
            opacity: 0


/* catalog page */
.main-scheme
  &__content
    @media (min-width: 1200px)
      display: flex
      flex-wrap: wrap
      align-items: stretch
    &__title-row
      @media (min-width: 1200px)
        flex: 1 1 100%
        width: 100%
        max-width: 100%
        margin-bottom: 50px
      .page-title
        display: inline-flex
        align-items: center
        justify-content: flex-start
      .icon
        display: inline-block
        margin-right: 10px
        font-size: 20px
        line-height: 20px
        color: $sdark-blue
        @media (min-width: 1200px)
          display: none
    &__left-side
      @media (min-width: 1200px)
        display: block !important
        flex: 0 1 23%
        max-width: 23%
    &__right-side
      margin-top: 20px
      @include adaptive-value('margin-top', 50, 20, 1)
      @media (min-width: 1200px)
        flex: 1 1 calc(77% - 30px)
        max-width: calc(77% - 30px)
        margin-left: 30px
        margin-top: 0
      &.catalog-page
        .catalog-page__tabs
          display: flex
          flex: 1 1 100%
          width: 100%
          &__item
            flex: calc(33.3% - 8px)
            max-width: calc(33.3% - 8px)
            margin: 0 12px 0 0
            transition: all .3s
            border: 1px solid transparent
            border-radius: 8px
            padding: 22px 0 18px
            text-align: center
            cursor: pointer
            &:hover
              border: 1px solid rgba(17, 72, 155, 0.5)
              box-shadow: 0px 2px 12px rgba(17, 72, 155, 0.12)

            @media (min-width: 641px)
              padding: 40px 30px 35px
            @media (min-width: 1024px)
              padding: 30px
            @media (min-width: 1200px)
              padding: 35px 30px
            &:nth-of-type(3)
              margin-right: 0
            .category-icon
              filter: invert(23%) sepia(20%) saturate(6230%) hue-rotate(202deg) brightness(92%) contrast(98%)
              display: block
              max-width: 45px
              margin: 0 auto
              @media (min-width: 641px)
                max-width: 50px
            .category-title
              font-family: Oswald, sans-serif
              font-size: 14px
              line-height: 14px
              letter-spacing: -.05em
              color: $dark-blue
              margin-top: 10px
              @media (min-width: 375px)
                font-size: 15px
                line-height: 15px
                letter-spacing: -.03em
                span
                  display: none
              @media (min-width: 415px)
                font-size: 16px
                line-height: 18px
              @media (min-width: 641px)
                font-size: 16px
                line-height: 22px
                text-transform: uppercase
              @media (min-width: 1024px)
                font-size: 18px
                line-height: 24px
              @media (min-width: 1200px)
                line-height: 24px
                letter-spacing: .5px
            &.active
              background-color: $asd
        .catalog-page__items-list
          margin-top: 12px
          transition: background-color .3s
          @media (min-width: 641px)
            margin-top: 20px
            .items-list-wrapper
              display: flex
              flex-wrap: wrap
              width: 100%
              max-width: 100%
              flex: 1 1 100%
          &__item
            padding: 7px 12px
            margin-bottom: 8px
            border-radius: 4px
            border: 1px solid transparent
            box-shadow: none
            transition: all .3s
            @media (min-width: 375px)
              margin-bottom: 10px
            @media (min-width: 577px)
              margin-bottom: 10px
              padding: 10px 15px
            @media (min-width: 641px)
              flex: calc(50% - 5px)
              max-width: calc(50% - 5px)
              margin-right: 10px
              &:nth-of-type(2n)
                margin-right: 0
            @media (min-width: 1200px)
              padding: 6px 12px
              margin-bottom: 14px
            &:hover
              border: 1px solid rgba(17, 72, 155, 0.5)
              box-shadow: 0px 2px 12px rgba(17, 72, 155, 0.12)

            &:last-child
              margin-bottom: 0
              @media (min-width: 641px)
                margin-bottom: 10px
            a
              display: block
              font-size: 15px
              line-height: 21px
              letter-spacing: .02em
              color: $sdark-blue
              text-decoration: none
              @media (min-width: 577px)
                font-size: 16px
                line-height: 22px
              @media (min-width: 1200px)
                font-size: 15px
                line-height: 21px
              span
                font-weight: 700
          &.active
            .catalog-page__items-list__item
              background-color: $asd
        .catalog-page__table
          max-width: 100%
          overflow-x: scroll
          margin-bottom: -35px
          .catalog-table
            border-spacing: 0px 12px
            width: 100%
            max-width: 100%
            tr
              background-color: $asd
              border-radius: 4px
              transition: all .3s
              &:nth-of-type(1)
                @media (min-width: 641px)
                  display: table-row !important
                  background-color: transparent
                  width: 100%
                  max-width: 100%
                  min-height: 45px
                  height: 45px
                  th
                    width: 100%
                    min-width: 80px
                    color: $sdark-blue
                    font-size: 15px
                    line-height: 21px
                    letter-spacing: .02em
                    font-weight: 400
                    border: 1px solid $blue2
                    border-right: none
                    white-space: nowrap
                    padding: 5px 15px
                    &:nth-of-type(1)
                      text-align: left
                    &:last-child
                      border-right: 1px solid $blue2

              &:hover
                box-shadow: 0px 2px 12px rgba(17, 72, 155, 0.12)
                td
                  border-color: rgba(17, 72, 155, 0.5) !important
                  &:nth-of-type(1)
                    a
                      color: $dark-blue
              td
                border-top: 1px solid transparent
                border-bottom: 1px solid transparent
                padding: 6px 12px
                display: inline-block
                min-height: 47px
                @media (min-width: 340px)
                  min-height: 56px
                @media (min-width: 641px)
                  display: table-cell !important
                  min-width: 90px
                  text-align: center
                  &:nth-of-type(1)
                    text-align: left
                &:nth-of-type(1)
                  display: inline-flex
                  align-items: center
                  justify-content: flex-start
                  flex-wrap: wrap
                  color: $sdark-blue
                  font-size: 15px
                  line-height: 21px
                  letter-spacing: .02em
                  border-radius: 4px 0 0 4px
                  border-left: 1px solid transparent
                  width: calc(100% - 68px)
                  @media (min-width: 640px)
                    width: auto
                  a
                    color: $sdark-blue
                    display: inline-block
                    font-weight: 700
                    transition: color .3s
                    max-width: 100%
                  p
                    width: 100%
                    max-width: 100%
                    display: block
                    white-space: nowrap
                    overflow: hidden
                    text-overflow: ellipsis
                    @media (min-width: 340px)
                      display: block !important
                &:last-child
                  border-radius: 0 4px 4px 0
                  border-right: 1px solid transparent
                  padding-left: 0
                  display: inline-flex
                  align-items: center
                  justify-content: center
                  @media (min-width: 641px)
                    padding: 6px 12px
                    text-align: center
                .buy-btn
                  display: inline-flex
                  padding: 9px 17px
                  align-items: center
                  justify-content: center
                  @media (min-width: 641px)
                    padding: 9px 35px
                  @media (min-width: 960px)
                    padding: 9px 50px
                  .icon
                    font-size: 20px
                    line-height: 20px
                    @media (min-width: 641px)
                      display: none
                  span
                    @media (min-width: 641px)
                      display: inline-block !important
                      color: #fff
                      font-size: 15px
                      line-height: 22px
                      font-weight: 500
                      letter-spacing: .025em
      &.product-page
        @media (min-width: 768px)
          display: flex
          flex-wrap: wrap
          align-items: stretch
        .product-content
          &__left-side
            @media (min-width: 768px)
              flex: 1 1 calc(100% - 390px)
              max-width: 470px
              width: 100%
              position: relative
            .product-image
              border-radius: 8px
              overflow: hidden
              @media (min-width: 768px)
                position: absolute
                top: 0
                left: auto
                right: auto
                height: 100%
                width: auto
          &__right-side
            margin-top: 15px
            @media (min-width: 415px)
              max-width: 470px
            @media (min-width: 768px)
              flex: 1 1 370px
              max-width: 370px
              width: 100%
              padding-left: 30px
              margin-top: 0
              .product-name
                display: block !important
                font-family: Oswald, sans-serif
                font-size: 25px
                line-height: 25px
                color: $sdark-blue
                font-weight: 700
                display: block
                margin-bottom: 15px

            .product-price
              text-transform: uppercase
              display: block
              color: $darky
              font-weight: 700
              font-size: 12px
              line-height: 15px
              letter-spacing: .5px
            .product-buy
              margin-top: 17px
              .icon
                margin-right: 3px
            .product-properties
              margin-top: 18px
              &__item
                display: flex
                justify-content: space-between
                color: $sdark-blue
                font-size: 15px
                line-height: 21px
                letter-spacing: .02em
                border-bottom: 1px solid $blue2
                padding: 8px 0
                &:last-child
                  border-bottom: none
                  padding-bottom: 0
          &__full-side
            margin-top: 35px
            color: $darky
            @media (min-width: 768px)
              flex: 1 1 100%
              max-width: 90%
              width: 100%
            h3
              font-family: Oswald, sans-serif
              font-size: 18px
              line-height: 24px
              font-weight: 700
              margin-bottom: 10px
            h4
              font-size: 15px
              line-height: 21px
              letter-spacing: .02em
              font-weight: 700
              margin-bottom: 10px
            p
              font-size: 15px
              line-height: 21px
              letter-spacing: .02em
              margin-bottom: 10px
            ul
              list-style-type: none
              margin: 0 0 10px
              padding: 0
              li
                display: inline-block
                position: relative
                text-indent: 14px
                margin-bottom: 2px
                &:last-child
                  margin-bottom: 0
                &:before
                  content: ''
                  display: block
                  width: 4px
                  height: 4px
                  border-radius: 50%
                  background-color: $darky
                  position: absolute
                  left: 3px
                  top: 9px
      &.news-page
        @media (min-width: 640px)
          display: flex
          flex-wrap: wrap
